import React from 'react';
import logo from '../../assets/images/logo.png';
import user from '../../assets/images/user.png';
import classes from './Header.module.css';
import { NavLink } from 'react-router-dom';

const Header = (props) => {
	return (
		<header className={classes.header}>
			<div className={classes.container}>
				<div className={classes.header__body}>
					<NavLink to='/profile' className={classes.header__logo}>
						<img src={logo} alt="logo" />
					</NavLink>
					<div className={`${classes.header__signup} ${classes.signup_header}`}>
						<div className={classes.signup_header__profile}>
							<div className={classes.loginBlock}>
								{props.isAuth
									? <div>{props.login} - <button onClick={props.logout}>Log out</button></div>
									: <NavLink to={'/login'}>Login</NavLink>}
							</div>
							{/*<div className={classes.signup_header__name}>Mary</div>*/}
							<img src={user} alt="face" width="30px" />
							<div className={classes.signup_header__arrow}></div>
						</div>
						{/*<div className={classes.signup_header__menu}>
							<a href="#" className={classes.signup_header__link}>My page</a>
							<a href="#" className={classes.signup_header__link}>Edit</a>
							<a href="#" className={classes.signup_header__link}>Settings</a>
							<a href="#" className={classes.signup_header__link}>Help</a>
							<a href="#" className={classes.signup_header__link}>Log out</a>
						</div>*/}
					</div>

				</div>
			</div>
		</header>
	);
}

export default Header;