import React from 'react';
import face from '../../../../../assets/images/face.webp';
import post01 from '../../../../../assets/images/post01.webp';
import heart from '../../../../../assets/images/heart.svg';
import comment from '../../../../../assets/images/comment.svg';
import share from '../../../../../assets/images/share.svg';
import eye from '../../../../../assets/images/eye.svg';
import classes from './Post.module.css';

const Post = (props) => {

	return (
		<div className={`${classes.posts__my_posts} ${classes.my_posts}`}>
			<div className={classes.my_posts__who}>
				<img src={face} alt="face" width="50px" />
				<div className={classes.my_posts__title}>
					<div className={classes.my_posts__namedate}>
						<h4 className={classes.my_posts__name}> {props.name} </h4>
						<p className={classes.my_posts__date}> {props.date} </p>
					</div>
					<div className={classes.my_posts__actions_arrow}></div>
				</div>
			</div>
			<div className={classes.my_posts__hashtag}>#car #incar #ride #onwheels #travelling #withfriends</div>
			<div className={classes.my_posts__message}> {props.message} </div>
			<div className={classes.my_posts__post}>
				<img src={post01} alt="post01" width="100%" />
			</div>
			<div className={classes.my_posts__like}>
				<div className={classes.my_posts__like_btns}>
					<img src={heart} alt="heart" width="20px" />
					{props.likesCount}
					<img src={comment} alt="comment" width="20px" />
					<img src={share} alt="share" width="20px" />
				</div>
				<div className={classes.my_posts__like_views}>
					<img src={eye} alt="eye" width="20px" />
					<span> {props.views} </span>
				</div>
			</div>
		</div>
	);
}

export default Post;