import { usersAPI } from '../api/api';
import { updateObjectInArray } from '../utils/object-helpers';

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET-USERS';
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL-USERS-COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE-IS-FOLLOWING-PROGRESS';

let initialState = {
	users: [],
	pageSize: 5,
	totalUsersCount: 0,
	currentPage: 1,
	isFetching: true,
	followingInProgress: []
};

const usersReducer = (state = initialState, action) => {
	switch (action.type) {
		case FOLLOW:
			return {
				...state,
				users: updateObjectInArray(state.users, action.id, "id", { followed: true })
			}
		case UNFOLLOW:
			return {
				...state,
				users: updateObjectInArray(state.users, action.id, "id", { followed: false })
			}
		case SET_USERS:
			return { ...state, users: action.users }
		case SET_CURRENT_PAGE:
			return { ...state, currentPage: action.currentPage }
		case SET_TOTAL_USERS_COUNT:
			return { ...state, totalUsersCount: action.count }
		case TOGGLE_IS_FETCHING:
			return { ...state, isFetching: action.isFetching }
		case TOGGLE_IS_FOLLOWING_PROGRESS: {
			return {
				...state,
				followingInProgress: action.isFetching
					? [...state.followingInProgress, action.id]
					: state.followingInProgress.filter(id => id !== action.id)
			}
		}
		default:
			return state;
	}
}

export const followSuccess = (id) => ({ type: FOLLOW, id })
export const unfollowSuccess = (id) => ({ type: UNFOLLOW, id })
export const setUsers = (users) => ({ type: SET_USERS, users })
export const setCurrentPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage })
export const setTotalUsersCount = (totalUsersCount) => ({ type: SET_TOTAL_USERS_COUNT, count: totalUsersCount })
export const toggleIsFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching })
export const toggleFollowingProgress = (isFetching, id) => ({ type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, id })

export const requestUsers = (page, pageSize) => {				// thunk-creator, который ретурнит thunk
	return async (dispatch) => {
		dispatch(toggleIsFetching(true));
		dispatch(setCurrentPage(page));

		let data = await usersAPI.getUsers(page, pageSize);

		dispatch(toggleIsFetching(false));
		dispatch(setUsers(data.items));
		dispatch(setTotalUsersCount(data.totalCount));
	}
}

const followUnfollowFlow = async (dispatch, id, apiMethod, actionCreator) => {
	dispatch(toggleFollowingProgress(true, id));
	let response = await apiMethod(id);

	if (response.data.resultCode === 0) {
		dispatch(actionCreator(id));
	}
	dispatch(toggleFollowingProgress(false, id));
}

export const follow = (id) => {
	return async (dispatch) => {
		let apiMethod = usersAPI.follow.bind(usersAPI);
		let actionCreator = followSuccess;
		followUnfollowFlow(dispatch, id, apiMethod, actionCreator);
	}
}

export const unfollow = (id) => {
	return async (dispatch) => {
		let apiMethod = usersAPI.unfollow.bind(usersAPI);
		let actionCreator = unfollowSuccess;
		followUnfollowFlow(dispatch, id, apiMethod, actionCreator);
	}
}

export default usersReducer;