const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {
	dialogs: [
		{ id: 1, name: 'Mary' },
		{ id: 2, name: 'Alex' },
		{ id: 3, name: 'Svetlana' },
		{ id: 4, name: 'Oleg' },
		{ id: 5, name: 'Nic' },
		{ id: 6, name: 'Rhyno' },
		{ id: 7, name: 'Sandra' },
		{ id: 8, name: 'Victor' }
	],
	messages: [
		{ id: 1, message: 'Hello world!', time: '11:08', name: 'Mary' },
		{ id: 2, message: 'Somebody watching you', time: '11:45', name: 'Svetlana' },
		{ id: 3, message: 'Yo-ho-ho', time: '13:15', name: 'Oleg' },
		{ id: 4, message: 'Where there are an equal number or fewer candidates than there are vacancies, all candidates are elected unopposed, and no poll is taken.', time: '22:01', name: 'Mary' }
	]
}

const messagesReducer = (state = initialState, action) => {
	switch (action.type) {
		case SEND_MESSAGE:
			let body = action.newMessageBody;
			return {
				...state,
				messages: [...state.messages, { id: 5, message: body, time: '11:11', name: 'Ya' }],
			}
		default:
			return state;
	}
}

export const sendMessageCreator = (newMessageBody) => ({ type: SEND_MESSAGE, newMessageBody })
export default messagesReducer;