import React from 'react';
import classes from './ProfileInfo.module.css';
import Preloader from '../../../common/Preloader/Preloader';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';

const ProfileInfo = ({ profile, status, updateStatus }) => {

	if (!profile) {
		return <Preloader />
	}

	return (
		<div className={`${classes.mainbar__profile} ${classes.profile_mainbar}`}>
			<img src={profile.photos.large} alt="face" className={classes.profile_mainbar__img} width="180px"></img>
			{/*<img src={face} alt="face" className={classes.profile_mainbar__img} width="180px"></img>*/}

			<ProfileStatusWithHooks status={status} updateStatus={updateStatus} />

			<div className={classes.profile_mainbar__info}>
				{/*<h4 className={classes.profile_mainbar__title}>Mary Moscow</h4>*/}
				<h4 className={classes.profile_mainbar__title}>{profile.fullName}</h4>
				<div className={classes.profile_mainbar__description}>
					<div className={classes.profile_mainbar__row}>
						<div className={classes.profile_mainbar__row_label}>City:</div>
						<div className={classes.rofile_mainbar__row_desc}>Moscow</div>
					</div>
					<div className={classes.profile_mainbar__row}>
						<div className={classes.profile_mainbar__row_label}>Github:</div>
						<div className={classes.rofile_mainbar__row_desc}>{profile.github}</div>
					</div>
					<div className={classes.profile_mainbar__row}>
						<div className={classes.profile_mainbar__row_label}>Website:</div>
						{/*<div className={classes.rofile_mainbar__row_desc}>instagram.com</div>*/}
						<div className={classes.rofile_mainbar__row_desc}>{profile.contacts.instagram}</div>
					</div>
					<div className={classes.profile_mainbar__row}>
						<div className={classes.profile_mainbar__row_label}>Info:</div>
						{/*<div className={classes.rofile_mainbar__row_desc}>Русский, Английский</div>*/}
						<div className={classes.rofile_mainbar__row_desc}>{profile.lookingForAJobDescription}</div>
					</div>
				</div>
			</div>
		</div>
	)
}

export default ProfileInfo;
