import React from 'react';
import classes from './SidebarNavLink.module.css';
import { NavLink } from 'react-router-dom';

const SidebarNavLink = (props) => {
	return (
		<li>
			<NavLink to={props.path} className={classes.list_sidebar__link}>
				<span className={classes.list_sidebar__icon}></span>
				<span className={classes.list_sidebar__signature}>{props.text}</span>
			</NavLink>)
		</li>
	);
}

export default SidebarNavLink;