const SET_LINKS = 'SET-LINKS';

let initialState = {
	linksNav: [
		{ id: 1, path: '/profile', text: 'My Profile' },
		{ id: 2, path: '/dialogs', text: 'Dialogs' },
		{ id: 3, path: '/friends', text: 'Friends' },
		{ id: 4, path: '/albums', text: 'Albums' },
		{ id: 5, path: '/groups', text: 'Groups' },
		{ id: 6, path: '/news', text: 'News' }
	]
}

const sidebarnavReducer = (state = initialState, action) => {
	switch (action.type) {
		case SET_LINKS: {
			return { ...state }
		}
		default:
			return state;
	}
}

export const setLinks = () => ({ type: SET_LINKS });

export default sidebarnavReducer;