import React from 'react';
import face from './../../../../assets/images/face.webp';
import classes from './Posts.module.css';
import Post from './Post/Post';
import { Field, reduxForm } from 'redux-form';
import { required, maxLengthCreator } from '../../../../utils/validators/validators';
import { Textarea } from '../../../common/FormsControls/FormsControls';

const maxLength10 = maxLengthCreator(10);

let AddNewPostForm = (props) => {
	return (
		<div>
			<form onSubmit={props.handleSubmit}>
				<div className={classes.posts__box}>
					<a href="index.html" className={classes.posts__user_link}>
						<img src={face} alt="face" width="30px"></img>
					</a>
					<div>
						<Field name="newPostText" placeholder={"Что у вас нового?"} component={Textarea} validate={[required, maxLength10]} ></Field>
					</div>
				</div>

				<button>Send</button>
			</form>
		</div>
	)
}

let AddNewPostFormRedux = reduxForm({ form: "ProfileAddNewPostForm" })(AddNewPostForm);

const Posts = React.memo(props => {

	let postsElements = [...props.posts]
		.reverse()
		.map(p => <Post date={p.date} name={p.name} message={p.message} views={p.views} likesCount={p.likesCount} addPost={props.addPost} updateNewPostText={props.updateNewPostText} key={p.id} />)

	let onAddPost = (values) => {
		props.addPost(values.newPostText);
	}

	return (
		<div className={`${classes.mainbar__posts} ${classes.posts}`}>
			<div className={classes.posts__body}>

				<AddNewPostFormRedux onSubmit={onAddPost} />
				{postsElements}

			</div>
		</div>
	)
});

export default Posts;
