import React from 'react';
import { NavLink } from 'react-router-dom';
import classes from './Friends.module.css';
import MyFriends from './MyFriends/MyFriends';

const Friends = (props) => {

	let state = props.myFriendsPage;
	let myFriendsElements = state.dialogs.map(f => <MyFriends name={f.name} key={f.id} />);

	return (
		<div>
			<div className={classes.friends__topblock}>
				<NavLink to="/users" className={classes.friends__search_button}>
					<button>Find Friends</button>
				</NavLink>
			</div>
			<div className={classes.friends__searchblock}>
				<input type="text" placeholder="Search Friends" />
			</div>

			{myFriendsElements}

		</div>
	)
}

export default Friends;