import React from 'react';
import Sidebar from './Sidebar/Sidebar';
import MainbarContainer from './Mainbar/MainbarContainer';
import FriendsContainer from './Friends/FriendsContainer';
/*import DialogsContainer from './Dialogs/DialogsContainer';
import UsersContainer from './Users/UsersContainer';
import Albums from './Albums/Albums';
import Groups from './Groups/Groups';
import News from './News/News';*/
import classes from './Maincontainer.module.css';
import { Route } from 'react-router-dom';
import Login from './Login/Login';
import { Suspense } from 'react';
import Preloader from '../common/Preloader/Preloader';
import { withSuspense } from '../../hoc/withSuspense';

const DialogsContainer = React.lazy(() => import('./Dialogs/DialogsContainer'));
const UsersContainer = React.lazy(() => import('./Users/UsersContainer'));
const Albums = React.lazy(() => import('./Albums/Albums'));
const Groups = React.lazy(() => import('./Groups/Groups'));
const News = React.lazy(() => import('./News/News'));

const Maincontainer = (props) => {
	return (
		<div className={classes.container}>
			<div className={classes.body_wrap}>
				<Sidebar />
				<div className={classes.mainbar_wrap}>
					<Route path="/profile/:userId?" render={() => <MainbarContainer />} />
					<Route path="/dialogs" render={withSuspense(DialogsContainer)} />
					<Route path="/friends" render={() => <FriendsContainer />} />
					<Route path="/albums" render={withSuspense(Albums)} />
					<Route path="/groups" render={withSuspense(Groups)} />
					<Route path="/news" render={withSuspense(News)} />
					<Route path="/users" render={withSuspense(UsersContainer)} />
					<Route path="/login" render={() => <Login />} />
				</div>
			</div>
		</div>
	);
}

export default Maincontainer;