import React from 'react';
import classes from './SidebarNav.module.css';
import SidebarNavLink from './SidebarNavLink/SidebarNavLink';

const SidebarNav = (props) => {

	let state = props.sidebarnavPage;
	let navbarElements = state.linksNav.map(l => <SidebarNavLink path={l.path} text={l.text} key={l.id} />);

	return (
		<nav className={`${classes.sidebar__list} ${classes.list_sidebar}`}>
			<ul>
				{navbarElements}
			</ul>
		</nav>
	)
}

export default SidebarNav;