import { profileAPI, usersAPI } from '../api/api';

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const SET_STATUS = 'SET-STATUS';
const DELETE_POST = 'DELETE-POST';

let initialState = {
	posts: [
		{ id: 1, date: '1 июля 2020', name: 'Mary Moscow', message: '', views: '16', likesCount: '3' },
		{ id: 2, date: '28 июня 2020', name: 'Пушистый Кот', message: '', views: '45', likesCount: '35' },
		{ id: 3, date: '6 июня 2020', name: 'Анна Иванова', message: '', views: '156', likesCount: '86' }
	],
	profile: null,
	status: ""
}

const profileReducer = (state = initialState, action) => {
	switch (action.type) {
		case ADD_POST: {
			let newPost = {
				id: 4,
				date: '2 июля 2020',
				name: 'Ann Smith',
				message: action.newPostText,
				views: 0,
				likesCount: 0
			};
			return {
				...state,
				posts: [...state.posts, newPost],
				newPostText: ''
			}

		}
		case SET_USER_PROFILE: {
			return { ...state, profile: action.profile }
		}
		case SET_STATUS: {
			return { ...state, status: action.status }
		}
		case DELETE_POST:
			return { ...state, posts: state.posts.filter(p => p.id !== action.postId) }
		default:
			return state;
	}
}

export const addPostActionCreator = (newPostText) => ({ type: ADD_POST, newPostText })
export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile })
export const setStatus = (status) => ({ type: SET_STATUS, status })
export const deletePost = (postId) => ({ type: DELETE_POST, postId })

export const getUserProfile = (id) => async (dispatch) => {
	const response = await usersAPI.getProfile(id);

	dispatch(setUserProfile(response.data));
}

export const getStatus = (id) => async (dispatch) => {
	const response = await profileAPI.getStatus(id);

	dispatch(setStatus(response.data));
}

export const updateStatus = (status) => async (dispatch) => {
	const response = await profileAPI.updateStatus(status)

	if (response.data.resultCode === 0) {
		dispatch(setStatus(status));
	}
}

export default profileReducer;
