const OPEN_WRITE_MESSAGE_MODULE_WINDOW = 'OPEN-WRITE-MESSAGE-MODULE-WINDOW';

let initialState = {
	dialogs: [
		{ id: 1, name: 'Mary' },
		{ id: 2, name: 'Alex' },
		{ id: 3, name: 'Svetlana' },
		{ id: 4, name: 'Oleg' },
		{ id: 5, name: 'Nic' },
		{ id: 6, name: 'Rhyno' },
		{ id: 7, name: 'Sandra' },
		{ id: 8, name: 'Victor' }
	]
}

const myFriendsReducer = (state = initialState, action) => {
	switch (action.type) {
		case OPEN_WRITE_MESSAGE_MODULE_WINDOW:
			return { ...state }
		default:
			return state;
	}
}

export const openWriteMessageModuleWindowActionCreator = () => ({ type: OPEN_WRITE_MESSAGE_MODULE_WINDOW });

export default myFriendsReducer;